import "bootstrap/dist/css/bootstrap.min.css";
import "./App.css";
import { useEffect, useState } from "react";

function App() {
  const [users, setUsers] = useState([]);
  const [posts, setPosts] = useState([]);
  const [comments, setComments] = useState([]);

  useEffect(() => {
    fetch("https://jsonplaceholder.typicode.com/users")
      .then((res) => res.json())
      .then((data) => {
        setUsers(data);
      });
  }, []);

  const getPosts = async (evt) => {
    const postData = await fetch(
      `https://jsonplaceholder.typicode.com/posts?userId=${evt.target.dataset.id}`
    );
    const getPostData = await postData.json();
    setPosts(getPostData);
  };

  const getComments = async (evt) => {
    const commentData = await fetch(
      `https://jsonplaceholder.typicode.com/posts/${evt.target.dataset.id}/comments`
    );
    const getCommentData = await commentData.json();
    setComments(getCommentData);
  };

  return (
    <>
      <div className="container">
        <header>
          <div className="row d-flex align-items-center justify-content-between mt-4">
            <div className="col-4">
              <a className="" href="">
                <b>Users</b>
              </a>
              <a href="">Home</a>
              <a href="">Link</a>
              <a href="">Contacts</a>
            </div>
            <div className="col-4 d-flex">
              <input
                type="text"
                className="form-control me-2"
                placeholder="search"
              />
              <button className="btn btn-outline-success">Search</button>
            </div>
          </div>
        </header>
        <h1 className="h2 text-center mt-3">Users API</h1>
        <div className="row py-3  border-top my-5">
          <div className="col-4">
            <h2 className="h3 m-0">Users</h2>
            <p className="user_count count m-0 mb-2"></p>
            <ul className="js-users-list list-unstyled lists">
              {users.map((user) => (
                <li
                  key={user.id}
                  className="border border-primary ps-2  mb-2 rounded"
                >
                  <a
                    className="user_name fw-bold fs-5 userData"
                    data-id={user.id}
                    onClick={getPosts}
                  >
                    {user.name}
                  </a>
                  <div>
                    <span className="fw-bold">ID:</span>
                    <span className="user_id userData">{user.id}</span>
                  </div>

                  <div>
                    <span className="fw-bold">Email: </span>
                    <a className="user_email userData" href="mailto:">
                      {user.email}
                    </a>
                  </div>
                  <div>
                    <span className="fw-bold">Country: </span>
                    <span className="user_country userData">
                      {user.address["city"]}
                    </span>
                  </div>
                  <div>
                    <span className="fw-bold">Company: </span>
                    <span className="user_company userData">
                      {user.company["name"]}
                    </span>
                  </div>
                  <div>
                    <span className="fw-bold">Website: </span>
                    <a
                      className="user_website userData"
                      href="https/"
                      target="_blank"
                    >
                      {user.website}
                    </a>
                  </div>
                </li>
              ))}
            </ul>
          </div>

          <div className="col-4 border-start border-end border-info">
            <h3 className="h3 m-0">Posts</h3>
            <p className="post_count count m-0 mb-2">Select user</p>
            <ul className="js-posts-list p-0 list-unstyled lists">
              {posts.map((post) => (
                <li
                  key={post.id}
                  className="border border-primary ps-2 border mb-2 rounded"
                >
                  <a
                    className="post_title fw-bold fs-5 postData"
                    data-id={post.id}
                    onClick={getComments}
                  >
                    {post.title}
                  </a>
                  <div>
                    <span className="fw-bold">User ID:</span>
                    <span className="user_id postData">{post.userId}</span>
                  </div>

                  <div>
                    <span className="fw-bold">Post: </span>
                    <span className="user_post postData">{post.body}</span>
                  </div>
                </li>
              ))}
            </ul>
          </div>

          <div className="col-4">
            <h3 className="h3 m-0">Comments</h3>
            <p className="comment_count count m-0 mb-2">Select post</p>
            <ul className="js-comments-list list-unstyled lists">
              {comments.map((comment) => (
                <li
                  key={comment.id}
                  class="border border-primary ps-2 border mb-2 rounded"
                >
                  <div>
                    <span class="fw-bold">Title: </span>
                    <b class="comment_name commentData">{comment.name}</b>
                  </div>
                  <div>
                    <span class="fw-bold">Post ID:</span>
                    <span class="post_id commentData">{comment.postId}</span>
                  </div>
                  <div>
                    <span class="fw-bold">Email: </span>
                    <a class="comment_email commentData" href="mailto:">
                      {comment.email}
                    </a>
                  </div>
                  <div>
                    <span class="fw-bold">Comment: </span>
                    <span class="comment_body commentData">{comment.body}</span>
                  </div>
                </li>
              ))}
            </ul>
          </div>
        </div>
      </div>
    </>
  );
}

export default App;
